.contato-home-container {
    width: 100%;
    height: 640px;

    background: rgb(218, 196, 73);

    background-image: url("../../../img/scholae_background2.png");

    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    
}

.form-title-container-home {
    width: 100%;
    max-width: 550px;

    margin-left: auto;
    margin-right: auto;

    
    
    
    
}

.form-container {
    
    width: 100%;
    max-width: 580px;
    
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    background: transparent;


    
    margin-top: -20px;
    
    
    display: flex;
    flex-direction: column;
}
  
  .title-form {
      text-align: center;
      color: #2b73e0;
      font-size: 68px;
      font-family: 'Concert One', cursive;
      text-transform: uppercase;

}
  
  .input-form-home {
    margin-top: 12px;  
    height: 52px;
    border-radius: 50px;
    border: 1px solid #ddd;
    padding-right: 12px;
    padding-left: 12px;
    font-size: 15px;
}

  .msg-form-home {
    margin-top: 12px; 
    height: 100px;
    border-radius: 14px;
    border: 1px solid #ddd;
    
    padding-bottom: 40px;
    padding-left: 12px;


    font-size: 15px;
}
  
.btn-enviar-form-home {
    margin-top: 12px; 
    padding: 10px 20px;
    border-radius: 14px;
    border: 0;
    background: rgb(50, 106, 228);
    color: #FFF;
    font-size: 17px;
    font-weight: bold;
    cursor: pointer;
  
}

@media screen and (max-width: 580px) {
    .contato-home-container {
        
        height: 765px;
    }

}